nav {
    display: flex;
    justify-content: center;
    gap: 20px;
    background-color: #333;
    padding: 10px;
}

nav a {
    color: white;
    text-decoration: none;
    font-size: 18px;
}

nav a.active {
    color: #4CAF50;
    font-weight: bold;
}

nav a:hover {
    color: #ddd;
}